	//content: ""
	//display: inline-block
	//background-repeat: no-repeat
	//background-position: center left
	//margin-right: 8px
	//width: 17px
	//height: 17px
	&__input {
		display: none; }
	&-list {
		display: flex;
		flex-wrap: wrap;
		&__item {
			position: relative;
			padding: 0;
			&[class*=col-] {
				padding: 0 15px;
				.file-upload__remove {
					right: 22px; } } } }
	&__remove {
		width: 30px;
		height: 30px;
		display: flex;
		align-items: center;
		justify-content: center;
		background-color: var(--color-4);

		position: absolute;
		right: 7px;
		bottom: 7px;
		cursor: pointer; } }

.custom {
	&-checkbox {}
	&-control {
		padding-left: 0;
		&-input {
			display: none; }
		&-label {
			border-color: var(--color-20);
			display: flex;
			align-items: center;
			&:before {
				width: 22px;
				height: 22px;
				border-radius: 2px;
				margin-right: 10px;
				top: 0;
				left: 0;
				position: relative; }
			&:after {
				display: none; } } } }


.custom-control-input:checked~.custom-control-label::before {
	background-image: url("data:image/svg+xml,%3Csvg width='11' height='10' viewBox='0 0 11 10' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1 4.55109L3.97067 8L10 1' stroke='%23737373' stroke-width='2'/%3E%3C/svg%3E%0A");
	background-position: center center;
	background-repeat: no-repeat;
	background-color: var(--color-white);
	border-color: var(--color-20); }
